<style type="text/css">
	
	#zixun-content{
		display: flex;
		height: 100%;
		flex-direction: column;

		.header{
			height: 44px; 
			width: 100%;
			display: flex;
			align-items: center;
			text-align: center;
			background: url('http://127.0.0.1/images/header_bg_mid.png') center;
			background-size: 100%;

			.back{
				width: 60px;

				img{
					height:20px;
				}
			}
			.title{
				flex: 1;
				color: #baac81; 
			}
			.more{
				width: 60px;
				img{
					height: 10px;
				}
			}
		}

		iframe{
			padding:0;
			border:0;
			margin:0;
			width: 100%;
			flex:1;
		}

		.footer{
			height: 30px;
			border-top: 1px solid #eee;
			width: 100%;
			display: flex;
			align-items: center;
			background: #fff;

			.conSwiper{
				flex:1;
				padding: 0 5px;
				height: 30px;
				color: #888;

				img{
					width: 20px;
					border-radius: 10px;
					vertical-align: sub;
				}
			}
			.comment,.editor{
				width: 30px;
				img{
					width: 20px;
				}
			}

		}
	}	

</style>

<template>
<div id="zixun-content">
	<div class="header">

		<span class="back" v-on:click="backPage">
			<img src="http://127.0.0.1/images/nav_back_normal.png">
		</span>
		<span class="title">资讯详情</span>
		<span class="more"><img src="http://127.0.0.1/images/menu_more.png"></span>
	</div>

	<iframe v-bind:src="iframeurl"></iframe>

	<div class="footer">

		<div class="swiper-container conSwiper">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for=" com in comment">
		        	<img v-bind:src="com.img">
		        	<span>{{com.cat}}</span>
		        </div>
		    </div>
		</div>

		<div class="comment">
			<img src="http://127.0.0.1/images/news_comments_num.png">
		</div>	
		<div class="editor">
			<img src="http://127.0.0.1/images/news_comment_publish_icon.png">
		</div>
	</div>
</div>


	
</template>


<script type="text/javascript">

var that;

export default {
	data: function(){
		return {
			iframeurl: '',
			comment: [
				{
					img: 'http://127.0.0.1/images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				},
				{
					img: 'http://127.0.0.1/images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				},
				{
					img: 'http://127.0.0.1//images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				},
				{
					img: 'http://127.0.0.1//images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				},
				{
					img: 'http://127.0.0.1/images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				},
				{
					img: 'http://127.0.0.1/images/teemo_1.png',
					cat: '蚊子啊护发素佛阿飞大红'
				}
			]
		};
	},
	ready: function() {
		that = this;
		this.iframeurl =  this.$route.query.url ;

		var conSwiper = new Swiper('.conSwiper',{
			direction :'vertical',
			autoplay: 2000,
			speed: 300
		});
		
	},
	methods: {
		backPage:function(	){
			console.log( window);
			history.go(-1);
		}
	}



}	
</script>


